    @supports (color: color-mix(in lab, red, red)) {
      background: color-mix(in srgb, var(--background) 95%, transparent);
    }
  }
}
.glass {
  background: var(--background);
  opacity: 0.9;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}
